<template>
  <div class="regist_all">
      <div class="mi_regist">
          <div class="form">
              <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" label-position="top">
                <div class="img">
                  <img src="./../assets/img/icon-mi.png">     
                </div>                         
                <div class="title">
                  <h3></h3>
                </div>
                <el-form-item label="/" prop="region">
                    <el-select v-model="ruleForm.region" placeholder="">
                      <el-option label="" value="zhongguo"></el-option>
                      <el-option label="" value="meiguo"></el-option>
                    </el-select>
                    <p>/</p>
                </el-form-item>
                <div class="phone">
                  <el-form-item label="" prop="checkPass" >
                    <!-- v-model="value6" -->
                    <el-select v-model="ruleForm.phone" placeholder="+86" class="inp1">
                      <el-option
                        v-for="item in cities"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        <span style="float: right">{{ item.label }}</span>
                        <span style="float: left; color: #8492a6; font-size: 13px">{{ item.value }}</span>
                      </el-option>
                    </el-select>
                    <el-input v-model="ruleForm.checkPass" placeholder="" class="inp2" ></el-input>
                  </el-form-item>
                </div>
                
                <el-form-item label="" prop="verify" class='verify'>                                      
                    <input type="text" id="code_input" value="" v-model="ruleForm.verify" />
                    <div id="v_container"></div>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="submitForm('ruleForm')" id="my_button"></el-button>
                </el-form-item>
              </el-form>
          </div>
          
      </div>
  </div>
  
</template>

<script>

export default {
  data() {
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if(!Number.isInteger(Number(value))){

          callback(new Error(''));
        } else if (value.length>12||value.length<11) {
          callback(new Error('11'));
        } else {
          callback();
        }
      };
      return {
        input: '',
        select: '',
        cities: [{
          value: '',
          label: '+86'
        }, {
          value: '',
          label: '+886'
        }, {
          value: '',
          label: '+852'
        }, {
          value: 'Brazil',
          label: '+55'
        }, {
          value: 'India',
          label: '+91'
        }, {
          value: 'Indonesia',
          label: '+62'
        }],

        value6: '',
        ruleForm: {
          name: '',
          region:'',
          phone: '',
          checkPass: '',
          verify:'',
          delivery: false,
          type: [],
          resource: '',
          desc: '',        
        },
        rules: {
          region: [
            { required: true, message: '/', trigger: 'change' }
          ],
          name: [
            { required: true, message: '', trigger: 'blur' },
            { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
          ],
          phone: [
            { required: true, message: '', trigger: 'blur' }
          ],
          verify: [
            { required: true, message: '', trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
        },
        options: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }, {
          value: '4',
          label: ''
        }, {
          value: '5',
          label: ''
        }],
        value: ''
      }
  },
  methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      } 
  },
  mounted:function(){
    // 
        var verifyCode = new GVerify("v_container");
        // var a =document.getElementById("code_input").value;
        document.getElementById("my_button").onclick = function(){
          var res = verifyCode.validate(document.getElementById("code_input").value);
          // var res = verifyCode.validate("123");
          if(res){
            alert("");
          }else{
            alert("");
          }
        }
  }
}
</script>
<!-- <script type="text/javascript" src="../../assets/js/gVerify.js"></script> -->
<style scope>
  .regist_all{
    position: absolute;
    width: 100%;
    height: 100%;
    background: #f9f9f9;
    color: #666;
  }
  .mi_regist{
    width: 60%;
    margin:0 auto;
    height: 100%;
    background: #fff;
  }
  .mi_regist .form{
    width: 50%;
    margin:auto;
    height: auto;
    position: relative;
    top: 15%;
  }
  .regist_all .el-select{
    width: 100%;
  }
  .mi_regist .img img{
    margin: 0 auto;
    display: block;
  }
  .mi_regist .title h3{
    text-align: center;    
  }
  .mi_regist .img,.mi_regist .title{
    margin-bottom: 10%;
  }
  .regist_all .el-button--primary{
    width: 100%;
    background-color: #ff6700;
    border: none;
  }
  .regist_all .el-button--primary:hover {
      background: #ff936a;
  }
  .regist_all .region p{
    margin: 0
  }
  .regist_all .verify input{
    width: 45%;
  }
  .phone{
    margin-bottom: 22px;
  }
  .phone .inp1{
    width: 30%;
    float: left;
    
  }
  .phone .inp1 input{
    border-right: none;
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
  }
  .phone .inp2{
    width: 70%;
  }
  .phone .inp2 .el-input__inner{
    border-radius: 0
  }
  .verify #v_container{
    width: 200px;
    height: 50px;
    float: right;
  }
  .verify #v_container canvas{
    height: 40px;
    float: right;
  }
  
</style>




